:root {
  --color-main: #00b8ee;
}

$breakpoints: (
  'sm': 'screen and (max-width: 640px)',
  'md': 'screen and (min-width: 768px)',
  'lg': 'screen and (min-width: 1000px)',
  'xl': 'screen and (min-width: 1200px)',
) !default;
@mixin mq($breakpoint: md) {
  @media #{map-get($breakpoints, $breakpoint)} {
    @content;
  }
}


.cf::after {
  display: block;
  content: "";
  clear: both;
}

.inner {
  width: 960px;
  margin: 0 auto;

  @include mq(sm) {
    background-color: #fdd;
    width: 100%;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  height: 140px;
  align-items: center;

  &__title {
    @include mq(sm) {
      margin-bottom: 20px;
      padding: 20px 20px 0 80px;
      width: 100%;
    }
  }
  &__logo {
    @include mq(sm) {
      width: 100%;
    }
  }

  &__address {
    text-align: right;
  }

  @include mq(sm) {
    flex-wrap: wrap;
  }
}

.topImage {
  background-color: var(--color-main);
  text-shadow: 2px 2px 5px #fff,
    2px -2px 5px #fff,
    -2px 2px 5px #fff,
    -2px -2px 5px #fff;
  font-size: 2.1875rem;
  line-height: 1.428;
  letter-spacing: 0.05em;
  height: 350px;
  width: 100%;
  color: #082648;
  /* background: url(../images/main_visual.jpg) no-repeat;*/
  background: resolve('main_visual.jpg') no-repeat;
  padding: 140px 0px 0px 60px;
  margin-top: 30px;
  box-sizing: border-box;
}

.nav {
  &__list {
    display: flex;

    @include mq(sm) {
      flex-direction: column;
    }
  }

  &__listitem {
    flex: 1;
  }

  &__link {
    font-size: 1.5rem;
    color: #fff;
    background: #00b8ee;
    text-align: center;
    display: block;
    padding: 20px 0px 15px;
    border-right: 1px dotted #fff;
  }

  &__subLink {
    font-size: 0.875rem;
    display: block;
  }
}

/* news info asideを1列にする */
.wrap {
  display: flex;
  justify-content: space-around;
  padding: 30px 30px 110px 30px;

  @include mq(sm) {
    flex-wrap: wrap;
    padding: 0;
  }
}

.main {
  width: calc(960px - 220px);
  padding: 40px 0px 0px 25px;
  @include mq(sm) {
    width: 100%;
  }
}

.news {
  margin-bottom: 50px;

  &__title {
    background: url(../images/ico_dolphin.png) no-repeat;
    padding-left: 40px;
    font-size: 1.5rem;
    color: var(--color-main);
  }

  &__date {
    float: left;
    color: var(--color-main);
    width: 7em;
  }

  &__text {}
}

.info {
  &__title {
    background: url(../images/ico_dolphin.png) no-repeat;
    padding-left: 40px;
    font-size: 1.5rem;
    color: var(--color-main);
  }

  &__list {
    display: flex;
    justify-content: space-around;

    @include mq(sm) {
      flex-wrap: wrap;
    }
  }
}

.infoItem {

  width: 200px;

  &__pic {
    width: 200px;
  }

  &__title {
    padding: 30px 0 15px 10px;
    color: var(--color-main);
    font-size: 1.2rem;
  }

  &__text {
    padding: 0 10px;
    font-size: 0.75rem;
  }
}

.time {
  &__title {
    width: 220px;
    line-height: 40px;
    font-size: 1.7rem;
    text-align: center;
    background-color: #fffab6;
    color: var(--color-main);
  }

  &__list {
    margin: 10px 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__itemTitle {
    padding: 2px;
    width: 1.5rem;
    font-size: 1.5rem;
    background-color: var(--color-main);
    color: #fff;

    &--holiday {
      @extend .time__itemTitle;
      background-color: #f39801;
    }
  }

  &__itemDate {
    text-align: right;
    color: var(--color-main);
    font-size: 1.5rem;

    &--holiday {
      @extend .time__itemDate;
      color: #f39801;
    }
  }
}

.tel {
  margin-top: 50px;

  &__title {
    width: 220px;
    line-height: 40px;
    font-size: 1.7rem;
    text-align: center;
    background-color: #fffab6;
    color: var(--color-main);
  }

  &__inner {
    text-align: center;
  }

  &__number {
    line-height: 40px;
    font-size: 1.2rem;
    color: var(--color-main);
  }

  &__text {}

}


.aside {
  width: 220px;
}

.footer {
  background-color: #bce2f5;
  border-top: 5px solid #00b8ee;

  &__container {
    width: 960px;
    margin: 0 auto;
    padding: 20px 0 0 100px;
    position: relative;
    background-image: url(../images/footer_bg.png);
    background-repeat: no-repeat;
    background-position: 100% 100%;
    height: 220px;
    @include mq(sm) {
      width: auto;
    }
  }

  &__toTop {
    color: #207393;
    font-weight: bold;
    text-decoration: underline;

  }
}

.sitemap {
  padding-top: 10px;
  width: 900px;
  margin: auto 0;
  display: flex;
  justify-content: flex-start;

  &__list {
    width: 180px;
    @include mq(sm) {
      display: none;
    }
  }

  &__item {
    & > a {
      line-height: 2rem;
      text-decoration: underline;
      color: #207393;
    }
  }
}
